#root {
    position: relative;
    overflow: clip;
}

.section {
    .fp-overflow {
        width: 100% !important;
    }
}

.container {
    width: 100%;
    max-width: 144rem;
    margin: 0 auto;
    padding: 0 var(--gap-fluid);
    overflow: hidden;
}

svg,
.u-icon {
    width: 1em;
    height: 1em;
    display: inline-block;
    color: inherit;
    fill: currentColor;
}

.img-cover {
    display: block;
    position: relative;
    overflow: hidden;
    img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
    }
}

.img-contain {
    display: block;
    position: relative;
    overflow: hidden;
    img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: contain;
    }
}

.area-click {
    --indent: -.5rem;
    position: relative;
    &:before {
        content: '';
        position: absolute;
        top: var(--indent);
        right: var(--indent);
        bottom: var(--indent);
        left: var(--indent);
    }
}

.link-cover {
    &:before {
        width: 100%;
        height: 100%;
        content: '';
        position: absolute;
        top: 0;
        left: 0;
    }
}

.btn-reset {
    background: transparent;
    margin: 0;
    padding: 0;
    font-size: 0;
    outline: none;
    border: none;
    border-radius: 0;
}

.visually-hidden,
.visually-hidden-focusable:not(:focus):not(:focus-within) {
    width: 1px!important;
    height: 1px!important;
    margin: -1px!important;
    padding: 0!important;
    position: absolute!important;
    border: 0!important;
    clip: rect(0,0,0,0)!important;
    white-space: nowrap!important;
    overflow: hidden!important;
}

.font-family-accent {
    font-family: $ff-2;
}

.content {
    line-height: 1.4;
    > :first-child {
        margin-top: 0;
    }
    > :last-child {
        margin-bottom: 0;
    }
    p {
        margin: 0 0 1em;
    }
}

.btn {
    max-width: 100%;
    min-height: var(--btn-height);
    padding: var(--btn-padding-y) var(--btn-padding-x);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-style: normal;
    font-weight: 800;
    font-size: inherit;
    font-size: var(--btn-font-size);
    font-family: $ff-1;
    line-height: 1.2;
    text-align: center;
    text-transform: uppercase;
    border: none;
    border-radius: 0;
    outline: none;
    user-select: none;
    transition: all .24s;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    z-index: 0;
    &:hover {
        text-decoration: none;
    }
    &.disabled,
    &:disabled {
        background: #ccc;
        color: #000;
        opacity: .6;
        pointer-events: none;
    }
    &--theme {
        &-accent-outline {
            background: transparent;
            color: $color-accent-3;
            border: 2px solid $color-accent-1;
            border-radius: 1rem;
            &:hover {
                @media (min-width: $lg-mf) {
                    background: $color-accent-3;
                    color: $color-white-1;
                }
            }
            &:focus,
            &:active {
            }
        }
        &-accent-outline-fill {
            background: $color-gray-2;
            color: $color-accent-1;
            border: 2px solid $color-accent-1;
            border-radius: 1rem;
            &:hover {
                @media (min-width: $lg-mf) {
                    background: $color-accent-1;
                    color: $color-white-1;
                }
            }
            &:focus,
            &:active {
            }
        }
        &-accent-fill {
            background: $color-accent-1;
            color: $color-white-1;
            border-radius: 1rem;
            &:hover {
                @media (min-width: $lg-mf) {
                    background: $color-accent-3;
                }
            }
            &:focus,
            &:active {
            }
        }
    }
    &--size {
        &-sm {
            --btn-height: 4.8rem;
            --btn-padding-x: 2.2rem;
            --btn-padding-y: 1.2rem;
            --btn-font-size: var(--fs-7);
            @media (max-width: $md) {
                --btn-padding-x: 1.2rem;
            }
        }
        &-md {
            --btn-height: 5.6rem;
            --btn-padding-x: 3rem;
            --btn-padding-y: 1.7rem;
            --btn-font-size: var(--fs-7);
        }
    }
    &--width {
        &-100 {
            width: 100%;
        }
    }
    &__inner {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        z-index: 1;
    }
    &__title {
    }
}

.logo {
    @extend .area-click;
    display: inline-block;
    font-weight: normal;
    font-size: var(--fs-5);
    font-family: $ff-2;
    text-transform: uppercase;
    white-space: nowrap;
    transition: all .34s;
    transition-property: opacity;
    cursor: pointer;
    &:hover {
        text-decoration: none;
        @media (min-width: $lg-mf) {
            opacity: .6;
        }
    }
}

.sc-block {
    &__list {
        margin: -.7rem;
        padding: 0;
        display: flex;
        list-style-type: none;
        @media (max-width: $sm) {
            margin: -.9rem;
        }
    }
    &__item {
        margin: .7rem;
        padding: 0;
        @media (max-width: $sm) {
            margin: .9rem;
        }
    }
    &__link {
        background: $color-white-1;
        width: 4rem;
        height: 4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $color-accent-1;
        border-radius: 50%;
        transition: all .4s;
        @media (max-width: $sm) {
            width: 4.8rem;
            height: 4.8rem;
        }
        &:hover {
            text-decoration: none;
            @media (min-width: $lg-mf) {
                background: $color-accent-1;
                color: $color-white-1;
            }
        }
    }
    &__icon {
        width: 2rem;
        height: 2rem;
        display: block;
    }
}

.services-card {
    background: $color-white-1;
    padding: 4.8rem 3.6rem;
    border-radius: 2rem;
    @media (max-height: 900px) {
        padding: 2rem 3rem;
    }
    @media (max-width: $lg) {
        padding-top: 4rem;
        padding-bottom: 4rem;
    }
    &__inner {
    }
    &__counter {
        margin: -2.4rem 0 4.8rem;
        font-weight: normal;
        font-size: 16rem;
        line-height: 1;
        font-family: $ff-2;
        @media (min-width: $lg-mf) {
            margin-top: -2.2rem;
            margin-bottom: 4vh;
        }
        @media (max-height: 900px) {
            margin-bottom: 2rem;
            font-size: 14rem;
        }
        @media (max-width: $xl) {
            margin-top: -1rem;
            font-size: 12rem;
        }
        &:after {
            content: counter(services-card-counter);
            display: block;
            font: inherit;
        }
    }
    &__title {
        max-width: 50rem;
        min-height: 2.08em;
        margin: 0 0 2.4rem;
        line-height: 1.04;
        text-transform: uppercase;
        @media (max-width: $xl) {
            margin-bottom: 2rem;
            font-size: 2.8rem;
        }
        @media (max-width: $lg) {
            min-height: 0;
        }
    }
    &__description {
        margin: 0 2.6rem 0 0;
        font-weight: 500;
        font-size: var(--fs-5);
        color: rgba($color-accent-1, .7);
        @media (max-width: $xl) {
            font-size: 1.8rem;
        }
    }
}

.u-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    li {
        min-height: 2.4rem;
        margin: 0;
        padding: 0 0 0 3.4rem;
        display: flex;
        align-items: center;
        font-weight: 500;
        font-size: var(--fs-5);
        line-height: 1.08;
        position: relative;
        @media (max-width: $md) {
            min-height: 2rem;
            padding-left: 2.6rem;
        }
