.header {
    width: 100%;
    padding: 2.4rem 0;
    position: fixed;
    top: 0;
    left: 0;
    transition: all .4s;
    z-index: 12;
    @media (max-width: $md) {
        padding: 1.6rem 0;
    }
    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__dropdown {
        background: $color-white-1;
        display: flex;
        flex-shrink: 0;
        @media (max-width: $md) {
            width: 100%;
            padding: 0 var(--gap-fluid) var(--gap-fluid) var(--gap-fluid);
            position: absolute;
            top: 100%;
            left: 0;
            transition: all .4s;
            z-index: 1;
        }
        &:not(.is-show) {
            @media (max-width: $md) {
                opacity: 0;
                visibility: hidden;
            }
        }
    }
    &__link {
        @media (min-width: $md-mf) {
            min-width: 16.5rem;
        }
        @media (max-width: $md) {
            padding-right: 0;
            padding-left: 0;
            flex: 1;
        }
        &:not(:last-child) {
            margin-right: 1.2rem;
        }
    }
    &__btn-toggle {
        @extend .btn-reset;
        width: 3.6rem;
        height: 3.6rem;
        margin-left: auto;
        color: $color-accent-3;
        flex: 0 0 3.6rem;
        position: relative;
        transition: all .4s;
        cursor: pointer;
        @media (min-width: $md-mf) {
            display: none;
        }
        span {
            background: currentColor;
            width: 28px;
            height: 2px;
            margin: -1px 0 0 -14px;
            position: absolute;
            top: 50%;
            left: 50%;
            border-radius: 1px;
            transition: all .4s;
            &:nth-child(1) {
                transform: translateY(-6px);
            }
            &:nth-child(2) {
            }
            &:nth-child(3) {
                transform: translateY(6px);
            }
        }
        &.is-active span:nth-child(1) {
            transform: translateY(0) rotate(-45deg);
        }
        &.is-active span:nth-child(2) {
            transform: translateX(3rem);
            opacity: 0;
            visibility: hidden;
        }
        &.is-active span:nth-child(3) {
            transform: translateY(0) rotate(45deg);
        }
    }
}

body:not(.fp-viewing-intro) .header {
    transform: translateY(-10rem);
    opacity: 0;
    visibility: hidden;
}