.appeal-section {
    --section-gap-y: 4rem;
    --section-gap-y-: -4rem;
    background: $color-accent-1;
    min-height: 100vh;
    display: flex;
    color: $color-white-1;
    border-bottom: 1px solid $color-accent-1;
    position: relative;
    overflow: hidden;
    z-index: 0;
    @media (max-width: $lg) {
        height: auto !important;
    }
    .fp-overflow {
        padding: var(--section-gap-y) 0;
        @media (max-width: $lg) {
            --section-gap-y: 8rem;
            --section-gap-y-: -8rem;
        }
    }
    &__container {
        display: flex;
        align-items: center;
    }
    &__row {
        @media (max-width: $lg) {
        }
    }
    &__content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        @media (max-width: $lg) {
            max-width: 100%;
            flex: 0 0 100%;
        }
    }
    &__title {
        margin: auto 0 0;
        line-height: 1.065;
        transition: all .8s;
        transition-property: color;
        order: -1;
        @media (max-width: $lg) {
            max-width: 86rem;
            margin: 0 auto 5.6rem;
            text-align: center;
        }
        @media (max-width: $md) {
            max-width: 50rem;
        }
        span {
            color: $color-gray-2;
            transition: all .8s;
            transition-property: color;
        }
    }
    &__arrow {
        max-width: 67rem;
        margin-top: auto;
        opacity: .1;
        @media (max-width: $lg) {
            display: none;
        }
        img {
            width: 100%;
            height: auto;
            display: block;
        }
    }
    &__aside {
        display: flex;
        @media (max-width: $lg) {
            max-width: 100%;
            flex: 0 0 100%;
        }
    }
    &__block {
        background: $color-white-1;
        display: flex;
        flex-direction: column;
        padding: 3.6rem;
        color: $color-black-2;
        border-radius: 2rem;
        @media (max-width: $lg) {
            width: 100%;
        }
        @media (max-width: $md) {
            padding: 2rem;
        }
        &-title {
            margin: 2rem 0 2.4rem;
            padding-bottom: 2.4rem;
            text-transform: uppercase;
            border-bottom: 1px solid rgba($color-accent-1, .2);
            @media (max-width: $md) {
                margin: 2.2rem 0 2rem;
                padding-bottom: 2rem;
            }
        }
        &-price {
            margin: 0 0 4.8rem;
            display: flex;
            align-items: flex-end;
            font-size: 4.8rem;
            line-height: 1;
            @media (max-width: $md) {
                margin-bottom: 4rem;
            }
            &-new {
                margin-right: 1.2rem;
                font-family: $ff-2;
                @media (max-width: $md) {
                    margin-right: 1rem;
                    font-size: 3.6rem;
                }
            }
            &-old {
                font-weight: 500;
                font-size: 54.166666666%;
                position: relative;
                opacity: .7;
                &:after {
                    background: currentColor;
                    width: 100%;
                    height: 1px;
                    content: '';
                    color: inherit;
                    position: absolute;
                    top: 50%;
                    left: 0;
                }
            }
        }
        &-list {
            margin: 0 0 9.6rem;
            @media (max-height: 900px) {
                margin-bottom: 6vh;
            }
            @media (max-width: $md) {
                margin-bottom: 4.2rem;
            }
        }
        &-btn {
            width: 100%;
            margin-top: auto;
            padding-right: 0;
            padding-left: 0;
        }
    }
}