.meta-section {
    background: $color-white-1;
    min-height: 100vh;
    display: flex;
    position: relative;
    z-index: 0;
    @media (max-width: $lg) {
        height: auto !important;
        justify-content: flex-start;
    }
    .fp-overflow {
        padding: 8rem 0;
        @media (max-width: $lg) {
            min-height: 100vh;
            display: flex;
            flex-direction: column;
            overflow-x: hidden;
        }
    }
    &__bg {
        height: 100%;
        max-height: 100%;
        padding-top: 24rem;
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        position: absolute;
        right: 0;
        bottom: 0;
        user-select: none;
        pointer-events: none;
        z-index: -1;
        @media (max-width: $lg) {
            height: auto;
            max-height: auto;
            margin: auto -15% -8rem 0;
            padding-top: 0;
            position: relative;
        }
        img {
            width: auto;
            height: 100%;
            display: block;
            @media (max-width: $lg) {
                width: 113%;
                height: auto;
            }
        }
    }
    &__container {
        flex-shrink: 0;
    }
    &__title {
    	max-width: 67%;
    	margin: 0;
        @media (max-width: $lg) {
            max-width: 80rem;
            margin: 0 auto 8rem;
            text-align: center;
        }
        @media (max-width: $md) {
            max-width: 50rem;
        }
    	span {
    		color: $color-gray-1;
    	}
    }
}