.services-section {
    background: $color-accent-1;
    min-height: 100vh;
    display: flex;
    align-items: center;
    @media (max-width: $lg) {
        height: auto !important;
    }
    .fp-overflow {
        padding: 2rem 0;
        @media (max-width: $lg) {
            padding: 8rem 0;
        }
    }
    &__container {
        overflow: hidden;
    }
    &__title {
        margin: 0 0 4.5rem;
        span {
            color: $color-gray-1;
        }
    }
    &__list {
        margin: var(--gap-);
        display: flex;
        counter-reset: services-card-counter;
        flex-wrap: wrap;
        &-col {
            max-width: 33.33333%;
            padding: var(--gap);
            flex: 0 0 33.33333%;
            counter-increment: services-card-counter;
            @media (max-width: $lg) {
                max-width: 100%;
                flex-basis: 100%;
            }
            &--width-9 {
                max-width: 66.666666666%;
                flex-basis: 66.666666666%;
                @media (max-width: $lg) {
                    max-width: 100%;
                    flex-basis: 100%;
                }
            }
        }
        &-card {
            height: 100%;
        }
    }
}