  .header {
    padding: 1.6rem 0;
  }
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__dropdown {
  background: #F8F5F0;
  display: flex;
  flex-shrink: 0;
}
@media (max-width: 991px) {
  .header__dropdown {
    width: 100%;
    padding: 0 var(--gap-fluid) var(--gap-fluid) var(--gap-fluid);
    position: absolute;
    top: 100%;
    left: 0;
    transition: all 0.4s;
    z-index: 1;
  }
}
@media (max-width: 991px) {
  .header__dropdown:not(.is-show) {
    opacity: 0;
    visibility: hidden;
  }
}
@media (min-width: 992px) {
  .header__link {
    min-width: 16.5rem;
  }
}
@media (max-width: 991px) {
  .header__link {
    padding-right: 0;
    padding-left: 0;
    flex: 1;
  }
}
.header__link:not(:last-child) {
  margin-right: 1.2rem;
}
.header__btn-toggle {
  width: 3.6rem;
  height: 3.6rem;
  margin-left: auto;
  color: #3C4E3E;
  flex: 0 0 3.6rem;
  position: relative;
  transition: all 0.4s;
  cursor: pointer;
}
@media (min-width: 992px) {
  .header__btn-toggle {
    display: none;
  }
}
.header__btn-toggle span {
  background: currentColor;
  width: 28px;
  height: 2px;
  margin: -1px 0 0 -14px;
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 1px;
  transition: all 0.4s;
}
.header__btn-toggle span:nth-child(1) {
  transform: translateY(-6px);
}
.header__btn-toggle span:nth-child(3) {
  transform: translateY(6px);
}
.header__btn-toggle.is-active span:nth-child(1) {
  transform: translateY(0) rotate(-45deg);
}
.header__btn-toggle.is-active span:nth-child(2) {
  transform: translateX(3rem);
  opacity: 0;
  visibility: hidden;
}
.header__btn-toggle.is-active span:nth-child(3) {
  transform: translateY(0) rotate(45deg);
}

