:root {
    --fs-default: 1.4rem;
    --fs-1: 6.8rem;
    --fs-2: 6.8rem;
    --fs-3: 4rem;
    --fs-4: 2.4rem;
    --fs-5: 2.2rem;
    --fs-6: 1.8rem;
    --fs-7: 1.6rem;
    --gap: 1rem;
    --gap-: -1rem;
    --gap-fluid: 5rem;
    --gap-fluid-: -5rem;
}

@media (max-width: $xl) {
    :root {
        --gap-fluid: 2rem;
        --gap-fluid-: -2rem;
    }
}

@media (max-width: $md) {
    :root {
        --fs-1: 3.8rem;
        --fs-2: 3.8rem;
        --fs-3: 2.8rem;
        --fs-5: 1.8rem;
        --fs-7: 1.4rem;
        --gap: .8rem;
        --gap-: -.8rem;
        --gap-fluid: 1.6rem;
        --gap-fluid-: -1.6rem;
    }
}

@media (max-width: $sm) {
    :root {
        --fs-default: 1.1rem;
    }
}