.main-hero {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
    user-select: none;
    transition-delay: 0s;
    &__hand {
        position: absolute;
        img,
        svg,
        span {
            width: 100%;
            height: auto;
            display: block;
            object-fit: contain;
        }
        &--1 {
            width: 52.2rem;
            max-width: 27.2%;
            height: auto;
            bottom: -12rem;
            left: 0;
            @media (max-width: $lg) {
                width: 44vw;
                max-width: 40rem;
                bottom: 0;
            }
        }
        &--2 {
            width: 52.2rem;
            max-width: 27.2%;
            height: auto;
            right: 0;
            bottom: -12rem;
            @media (max-width: $lg) {
                width: 44vw;
                max-width: 40rem;
                bottom: 0;
            }
            .js-svg-inline {
                transform: scaleX(-1);
            }
        }
        &-credit {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0;
            transition: all .8s;
            transition-delay: .6s;
            img {
                width: 100%;
                height: 100%;
                display: block;
            }
        }
        &--1 &-credit {
            img {
                @media (max-width: $lg) {
                }
            }
        }
        &--2 &-credit {
            img {
                @media (max-width: $lg) {
                }
            }
        }
    }
    &__sun {
        width: 84rem;
        height: 84rem;
        margin-top: -31.8rem;
        border-radius: 50%;
        position: absolute;
        top: 100%;
        left: 50%;
        transform: translate(-50%, 0);
        transition: all 1s;
        transition-property: transform;
        pointer-events: none;
        z-index: -4;
        @media (max-width: $lg) {
            margin-top: -15rem;
        }
        &:before,
        &:after {
            width: 100%;
            height: 100%;
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            border-radius: inherit;
            transition: all 1.8s;
        }
        &:before {
            opacity: 0;
            visibility: hidden;
            z-index: -2;
        }
        &:after {
            background: $color-white-2;
            z-index: -1;
        }
    }
    &__scroll-link-wrap {
        display: flex;
        justify-content: center;
        position: absolute;
        right: 0;
        bottom: 0;
        left: 0;
        pointer-events: none;
        z-index: 1;
    }
    &__scroll-link {
        margin: 0 auto;
        padding: 0 0 10rem;
        display: block;
        transition: all .6s;
        opacity: 0;
        visibility: hidden;
        pointer-events: auto;
        @media (max-width: $lg) {
            padding: 0 0 8rem;
        }
        &:hover {
            text-decoration: none;
            @media (min-width: $lg-mf) {
                opacity: .4 !important;
            }
        }
        .icon {
            width: 6.2rem;
            height: 6.2rem;
            display: block;
            @media (max-width: $md) {
                width: 3.6rem;
                height: 3.6rem;
            }
        }
    }
    &__bg {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        transition: all 1s;
        z-index: -4;
    }
}

.hand-el-1 {
    transition: all 1.4s;
}

.hand-el-2 {
    transition: all 1.4s;
}

.hand-el-3 {
    transition: all 1.4s;
}

.hand-el-4 {
    transition: all 1.4s;
}

.fp-viewing-intro {
    .main-hero__scroll-link {
        visibility: visible;
        opacity: 1;
    }
}

.fp-viewing-intro-hero {
    .hand-el-1 {
        transform: translateX(-20rem) translateY(-15vh) rotate(70deg);
        opacity: 0;
        visibility: hidden;
    }
    .hand-el-2 {
        transform: translateX(14rem) translateY(14rem);
        opacity: 0;
        visibility: hidden;
    }
    .hand-el-3 {
        transform: translateY(100vh);
        opacity: 0;
        visibility: hidden;
    }
    .main-hero__hand-credit {
        transform: translateY(40vh);
        opacity: 0;
        visibility: hidden;
        transition-delay: 0s;
    }
    .mountains-section__hand {
        transform: none;
        opacity: 1;
        transition-delay: .4s;
        visibility: visible;
        @media (max-width: $lg) {
            width: 54vw;
            max-width: 40rem;
        }
    }
    .main-hero__bg {
        background: $color-white-2;
    }
    .main-hero__sun {
        transform: translate(-50%, -25.6rem);
        @media (max-width: $lg) {
            transform: translate(-50%, -8rem);
        }
        &:before {
            background: radial-gradient(50% 50% at 50% 50.00%, #EDAE46 0%, rgba(248,245,240, 0) 100%, rgba(255,255,255, 0) 100%);
            transform: scale(1.3);
            opacity: .8;
            visibility: visible;
        }
        &:after {
            background: $color-white-1;
        }
    }
}

body:not(.fp-viewing-intro-hero) .mountains-section__hand {
    transform: translateY(100vh);
    opacity: 0;
    visibility: hidden;
}

body:not(.fp-viewing-intro):not(.fp-viewing-intro-hero) .main-hero {
    visibility: hidden;
    transition-delay: 1s;
}

.intro-section {
    position: relative;
    @media (max-width: $lg) {
        height: auto !important;
    }
    .fp-overflow {
        display: flex;
    }
    &__container {
        width: 100%;
        height: 100%;
        margin: 0 auto;
        padding-top: 10.4rem;
        padding-bottom: 10.4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        overflow: hidden;
        @media (max-width: $lg) {
            padding-top: 15.4rem;
            padding-bottom: 0;
            justify-content: flex-start;
        }
    }
    &__content {
        max-width: 88rem;
        margin: 0 auto 10rem;
        text-align: center;
        @media (max-width: $md) {
            margin-top: 0;
            margin-bottom: auto;
            padding-bottom: 7rem;
        }
    }
    &__title {
        margin: 0 0 4.5rem;
        @media (max-width: $md) {
            max-width: 50rem;
            margin-right: auto;
            margin-bottom: 4.2rem;
            margin-left: auto;
        }
    }
    &__btn {
        min-width: 31.4rem;
        @media (max-width: $md) {
            min-width: 0;
        }
        @media (max-width: $xs) {
            width: 100%;
        }
    }
}