.mountains-section {
    position: relative;
    *,
    *:before,
    *:after {
        pointer-events: none;
        user-select: none;
    }
    &__bg-color {
    }
    &__bg {
        background-position: center bottom;
        background-size: 100% auto;
        background-repeat: no-repeat;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        &--1 {
            background-image: url(../images/mountains/mountains-1.svg);
            z-index: -2;
        }
        &--2 {
            background-image: url(../images/mountains/mountains-2.svg);
            z-index: 1;
        }
        &--3 {
            background-image: url(../images/mountains/mountains-3.svg);
            z-index: -3;
        }
    }
    &__hand {
        width: 80rem;
        height: auto;
        margin: 0 auto;
        position: absolute;
        right: 0;
        bottom: 0;
        left: 0;
        transition: all 1.2s;
        transition-delay: 0s;
        transform: translateY(100vh);
        opacity: 0;
        visibility: hidden;
        z-index: 0;
        @media (max-width: $lg) {
            width: 44vw;
            max-width: 40rem;
        }
        img,
        svg {
            width: 100%;
            height: 100%;
            display: block;
        }
    }
}