.footer {
    background: $color-white-2;
    @media (max-width: $sm) {
    }
    .fp-overflow {
        padding: 5.6rem 0;
    }
    &__container {
        overflow: visible;
    }
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width: $sm) {
            flex-direction: column;
            justify-content: center;
        }
    }
    &__logo {
        flex-shrink: 0;
        @media (max-width: $sm) {
            margin-bottom: 3.6rem;
        }
    }
    &__sc-block {
    }
    &__copyright {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        color: rgba($color-accent-1, .5);
        @media (max-width: $sm) {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
        }
        &-description {
            max-width: 111rem;
            margin: 0 var(--gap-fluid) 0 0;
            @media (max-width: $sm) {
                max-width: 100%;
                margin: 0 0 2rem;
            }
        }
        &-author {
            flex-shrink: 0;
            text-align: right;
            @media (max-width: $sm) {
                margin-bottom: -3.2rem;
                text-align: center;
            }
        }
    }
    &__divider {
        background: rgba($color-accent-1, .2);
        width: 100%;
        height: 1px;
        margin: 2.6rem 0;
        display: block;
        border: none;
        outline: none;
        @media (max-width: $sm) {
            margin: 2.4rem 0;
        }
    }
}